<template>
  <cube-tab-bar
    class="navBar"
    v-model="selectedNavTitle"
    @click="clickHandler"
    @change="changeHandler">
    <cube-tab
      :showSlider="showSlider"
      :inline="inline"
      :useTransition="useTransition"
      v-for="(item, index) in tabs"
      :label="item.label"
      :key="item.label">
      <!-- nameicon -->
      <i slot="icon" :class="item.icon" v-if="index==2"><b>{{$store.state.cartNum}}</b></i>
      <i slot="icon" :class="item.icon" v-else></i>
      <!--  -->
      {{item.label}}
    </cube-tab>
  </cube-tab-bar>
</template>

<script>
  import './../assets/style/cubeRest.css'
  export default {
    data () {
      return {
        selectedLabelSlots: '',
        tabs: [{
          label: '',
          icon: 'cubeic-home'
        }, {
          label: '',
          icon: 'cubeic-like'
        }, {
          label: '',
          icon: 'cubeic-vip'
        }, {
          label: '',
          icon: 'cubeic-person'
        }],
        showSlider: false,
        inline: true,
        useTransition: false
      }
    },
    props:{
      selectedNavTitle: String,
    },
    methods: {
      clickHandler (label) {
        // if you clicked home tab, then print 'Home'
//        this.$store.commit('changeTitle',label)
        switch (label){
          case '':
            this.$router.push('/')
            break
          case '':
            this.$router.push('/me/index')
            break
          case '':
            this.$router.push('/sort/index')
            break
          case '':
            this.$router.push('/cart/index')
            break
          default:
            this.$router.push('/')
        }

      },
      changeHandler (label) {
        // if you clicked different tab, this methods can be emitted
//        console.log(this.$route.path)
      }
    },
    mounted (){
//      this.clickHandler(this.selectedNavTitle)
    },
  }
</script>

<style scoped lang="less">
  .navBar{
    position:fixed;
    bottom:0;
    left:0;
    width:100%;
    font-size:24px;
    background-color:white;
    border-top:1px solid #eee;
    z-index: 99;
    .cube-tab{
      /*position:relative;*/
    }
    .cubeic-vip{
      position:relative;
    }
  }
  .navBar .cube-tab_active{
    color: #ff0207;
  }
  .cubeic-vip b{
    position:absolute;
    right:-5px;
    top:-5px;
    color:white;
    background-color:#ff0207;
    display: inline-block;
    border-radius: 50%;
    width:15px;
    height:15px;
    line-height:15px;
    font-size:10px;
  }
</style>



