import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    staticImg: '/static/img/',
    defaultTitle: '',
    IMGPATH: '',
    isLoading: true,
    cartNum: localStorage.cartNum?localStorage.cartNum:0,//
    // token
    Authorization: localStorage.getItem('Authorization') ? localStorage.getItem('Authorization') : ''
  },
  mutations: {
    // tokentokenlocalStorage
    changeLogin (state, user) {
      state.Authorization = user.Authorization;
      localStorage.setItem('Authorization', user.Authorization);
    },
    //
    changeTitle (state, title){
      state.defaultTitle = title
    },
    //ajax 
    changeLoading (state, val){
      state.isLoading = val
    },
    //
    changeCartNum (state, val){
      localStorage.cartNum = val
      state.cartNum = localStorage.cartNum
    }
  },
  actions: {
    // changeMallInfo(ctx,data){
    //   ctx.commit("changeMallInfo",data)
    // }
  }
})



